.container {
    position: fixed;
    background: #000;
    color: #00ff00aa;
    line-height: 30px;
    padding: 10px;
    overflow: hidden;
    box-shadow: 0px 0px 15px 1px #0f0;
    width: 100%;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    font-size: 26px;
}

.message {
    position: relative;
    display: inline-block;
}

.scoreDisplay {
    position: relative;
    float: right;
}

.title {
    position: relative;
    float: left;
}

@media only screen and (max-width: 1080px) {
    .message {
        display: block;
    }

    .scoreDisplay {
        display: block;
        float: none;
    }

    .title {
        display: block;
        float: none;
    }

}
